import {HttpClient, HttpHeaders, HttpParams} from '@angular/common/http';
import {Injectable} from '@angular/core';
import {Observable} from 'rxjs/Observable';
import {Subject} from 'rxjs/Subject';

@Injectable()
export class TerminalService {
  private BaseUrl = 'http://10.0.0.30:8000/';
  private headers = new HttpHeaders().set('Content-Type', 'application/json');
  private dates: string[];
  private notify = new Observable(x => {
    this.getDates().then(z => {
      x.next(z[0]);
      x.complete();
    });
  });
  private dateSubject = new Subject<string>();
  private titleSubject = new Subject<string>();

  constructor(private http: HttpClient) {
  }

  getDates(): Promise<Object> {
    const url = this.BaseUrl + 'listAllMonths';
    return this.http.get(url).toPromise();
  }

  getNotify(): Observable<string> {
    return this.notify;
  }

  getDateSubject(): Subject<string> {
    return this.dateSubject;
  }

  getTitleSubject(): Subject<string> {
    return this.titleSubject;
  }

  changeDate(date: string): void {
    this.dateSubject.next(date);
  }

  changeTitle(title: string): void {
    this.titleSubject.next(title);
  }


  getBrand(endTime: any) {
    const url = this.BaseUrl + 'getOneYearHbaseVO';
    const params = new HttpParams().set('end', endTime);
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

  getModel(endTime: any) {
    const url = this.BaseUrl + 'getOneYearHbaseVO';
    const params = new HttpParams().set('end', endTime).append('column', '1');
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

  getVersion(endTime: any) {
    const url = this.BaseUrl + 'getOneYearHbaseVO';
    const params = new HttpParams().set('end', endTime).append('column', '2');
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

  getSeller(endTime: any) {
    const url = this.BaseUrl + 'getOneYearHbaseVO';
    const params = new HttpParams().set('end', endTime).append('column', '3');
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

  getNetwork(endTime: any) {
    const url = this.BaseUrl + 'getOneYearHbaseVO';
    const params = new HttpParams().set('end', endTime).append('column', '4');
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }

  getResolution(endTime: any) {
    const url = this.BaseUrl + 'getOneYearHbaseVO';
    const params = new HttpParams().set('end', endTime).append('column', '5');
    const options = {params: params};
    return this.http.get(url, options).toPromise();
  }
}



