import {AfterViewInit, Component, OnDestroy, OnInit} from '@angular/core';
import {TERMINAL, Terminal} from '../terminal';
import {TerminalService} from '../terminal.service';


@Component({
  selector: 'ngx-brand',
  templateUrl: './brand.component.html',
  styleUrls: ['./brand.component.scss'],
})
export class BrandComponent implements OnInit {
  options: any = {};
  terminals: Terminal[] = TERMINAL;
  selectedTerminal: Terminal;
  mInstance: any;
  name: string;
  isShow: boolean = true;

  chartInit(ev): void {
    this.mInstance = ev;
  }

  onSelect(terminal: Terminal): void {
    this.selectedTerminal = terminal;
    this.name = terminal.name;
    this.options.series[0].data = this.selectedTerminal.degree;
    this.mInstance.setOption(this.options);
  }

  constructor(terminalService: TerminalService) {
    this.selectedTerminal = TERMINAL[0];
    this.name = this.selectedTerminal.name;
    terminalService.changeTitle('移动设备品牌');
    terminalService.getNotify().subscribe(x => {
      terminalService.getBrand(x).then(z => {
        this.isShow = true;
        this.options.xAxis[0].data = z['date'];
        this.terminals = z['phoneBrandDTOS'] as Terminal[];
        this.onSelect(this.terminals[0]);
      });
    });
    terminalService.getDateSubject().subscribe(x => {
      terminalService.getBrand(x).then(z => {
        this.options.xAxis[0].data = z['date'];
        this.terminals = z['phoneBrandDTOS'] as Terminal[];
        console.log(z['phoneBrandDTOS']);
        console.log(this.terminals);
        this.onSelect(this.terminals[0]);
      });
    });
  }


  ngOnInit() {
    this.options = {
      tooltip: {
        trigger: 'axis',
        formatter: function (params) {
          return params[0].name + '<br/>'
            + params[0].seriesName + ' : ' + params[0].value + ' %<br/>';
        },
      },
      toolbox: {
        show: true,
        feature: {
          mark: {show: true},
          dataView: {show: true, readOnly: false},
          magicType: {show: true, type: ['line', 'bar']},
          restore: {show: true},
          saveAsImage: {show: true},
        },
      },
      calculable: true,
      xAxis: [
        {
          type: 'category',
          boundaryGap: false,
          data: ['2016-06', '2016-08', '2016-10', '2016-12', '2017-02', '2017-04', '2017-06'],
        },
      ],
      yAxis: [
        {
          type: 'value',
          axisLabel: {
            formatter: '{value} %',
          },
        },
      ],
      series: [
        {
          name: '用户规模:',
          type: 'line',
          axisLabel: {
            formatter: '{value} %',
          },
          data: this.selectedTerminal.degree,
          itemStyle: {
            normal: {
              color: '#5d72ec', /*图标颜色*/
            },
          },
          lineStyle: {
            normal: {
              color: '#5d72ec', /*连线颜色*/
            },
          },
          markPoint: {
            data: [
              {type: 'max', name: '最大值'},
              {type: 'min', name: '最小值'},
            ],
          },
          markLine: {
            data: [
              {type: 'average', name: '平均值'},
            ],
          },
        },
      ],
    };
  }
}



