import {Component, OnInit} from '@angular/core';
import {TerminalService} from './terminal.service';

declare var $;

@Component({
  selector: 'ngx-terminal',
  templateUrl: './terminal.component.html',
  styleUrls: ['./terminal.component.scss'],
})
export class TerminalComponent {
  title: string = '终端分析';
  selected: string;
  terminalService: TerminalService;
  dates: string[] = [
    '2018-03', '2018-02', '2018-01', '2017-12', '2016-11', '2017-10', '2017-09', '2017-08', '2017-07',
  ];

  constructor(terminalService: TerminalService) {
    this.terminalService = terminalService;
    terminalService.getTitleSubject().subscribe(title => this.title = title);
    terminalService.getDates().then(x => {
      this.dates = x as string[];
      console.log(this.dates);
    });
  }

  handle(s: string): void {
    this.terminalService.changeDate(s);
  }
}



